function Face(e) {
  const scale = [0.6, 0.9];
  return (
    <div>
      <Parallax
        style={{
          transform: "translateY(30px)",
          opacity: 0,
          filter: "blur(0px)",
        }}
        animation={[
          { y: 0, opacity: 1, playScale: [0.2, 0.3] },
          { opacity: 0.2, blur: "20px", playScale: [0.6, 0.7] },
        ]}
      >
        <Lu />
        <Han />
      </Parallax>

      <div
        style={{
          display: "flex",
          flexDirection: "row",
          marginTop: "25%",
          alignItems: "start",
        }}
      >
        <Parallax
          style={{
            transform: "translateY(-60px)",
            fontFamily: "Song",
            fontSize: "18px",
            opacity: 0,
          }}
          animation={{ y: 0, opacity: 1, playScale: scale }}
        >
          今天是我们第几次
        </Parallax>
        <Parallax
          style={{
            fontFamily: "Song",
            fontSize: "72px",
            color: "#4864C5",
          }}
          animation={{ y: 0, fontSize: "18px", playScale: scale }}
        >
          见面
        </Parallax>
        <Parallax
          style={{
            transform: "translateY(-60px)",
            fontFamily: "Song",
            fontSize: "18px",
            opacity: 0,
          }}
          animation={{ y: 0, opacity: 1, playScale: scale }}
        >
          ?
        </Parallax>
      </div>
      <Parallax
        style={{
          fontFamily: "Song",
          fontSize: "12px",
          opacity: 0,
        }}
        animation={{ y: 0, opacity: 1, playScale: scale }}
      >
        How many times have we met this year ?
      </Parallax>
    </div>
  );
}

export { Face, Lu, Han };
